import React, { useState, useRef } from 'react';
import { Stack, Typography } from '@mui/material';
import RecordBoard from './components/RecordBoard';
import RecordSaveButton from './components/RecordSaveButton';
import RecordTitle from './components/RecordTitle';
import RecordUpper from './components/RecordUpper';
import AlertModal from '@/components/AlertModal';
import { useNavigate, useParams } from 'react-router-dom';
import SelectSnowballObject from '@/components/SelectSnowballObject';
import useAxiosWithAuth from '@/utils/useAxiosWithAuth';
import Layout from '@/layouts/Layout';
import { Helmet } from 'react-helmet-async';
import { useSnackbarStore } from '@/stores/useSnackbarStore';

const RecordForm = () => {
    const navigate = useNavigate();
    const { userId } = useParams();

    // useState  
    const [title, setTitle] = useState('');
    const [answer, setAnswer] = useState('');
    const [inputCount, setInputCount] = useState(0);
    const [image, setImage] = useState(null);
    const [shapeName, setObjectName] = useState('');
    const { setSnackbarOpen } = useSnackbarStore();
    const [openModal, setOpenModal] = useState(false);

    // RecordBoard  ()
    const recordBoardRef = useRef(null); // RecordBoard 
    const selectObjectRef = useRef(null); // SelectSnowballObject 

    const goToMain = () => navigate(`/main/${userId}?page=1`);

    //   
    const handleSetImage = (image) => {
        console.log('Setting image:', image);
        setImage(image);
    };

    //       
    const handleAnswerChange = (e) => {
        setAnswer(e.target.value.slice(0, 199));
        setInputCount(e.target.value.length);
    };

    //     &  
    const axiosInstance = useAxiosWithAuth();
    const handleAcceptModal = async () => {
        // FormData        
        const formData = new FormData();
        if (image) formData.append('image', image);

        console.log('image:', image);
        console.log('title:', title || 'Empty');
        console.log('answer:', answer || 'Empty');
        console.log('object_name:', shapeName || 'Empty');

        await axiosInstance
            .post(`/api/my_memory/write`, formData, {
                headers: {
                    'Content-Type': 'multipart/form-data',
                },
                params: {
                    title: title,
                    answer: answer,
                    shapeName: shapeName,
                },
            })
            .then(() => {
                navigate(`/mycomplete/${userId}`);
                console.log('Memory successfully uploaded');
            })
            .catch((error) => {
                console.log('Error:', error);
                setSnackbarOpen({
                    text: '  .  .',
                    severity: 'error',
                });
            });
    };

    //    
    const handleCloseModal = () => {
        setOpenModal(false);
    };

    //    
    const handleSubmit = (e) => {
        e.preventDefault();

        //   
        if (!shapeName) {
            setSnackbarOpen({
                text: '  .',
                severity: 'warning',
            });
            selectObjectRef.current.scrollIntoView({ behavior: 'smooth' });
            return;
        } else if (!answer) {
            setSnackbarOpen({
                text: '  .',
                severity: 'warning',
            });
            recordBoardRef.current.scrollIntoView({ behavior: 'smooth' });
            return;
        }

        // text    
        setOpenModal(true);
    };

    return (
        <Layout
            snow
            overlay
            sx={{
                py: 3,
            }}
        >
            <Helmet>
                <title> -  </title>
                <meta
                    name='description'
                    content='  .'
                />
                <meta property='og:title' content=' -  ' />
                <meta
                    property='og:description'
                    content='  .'
                />
                <meta property='og:type' content='website' />
            </Helmet>
            <Stack sx={contentstyle}>
                <Stack ref={selectObjectRef}>
                    <Stack>
                        <RecordUpper sx={{ float: 'left' }}></RecordUpper>
                    </Stack>
                    <Stack>
                        <SelectSnowballObject
                            snowballObject={shapeName}
                            setSnowballObject={setObjectName}
                            mine
                        />
                    </Stack>
                    <Stack ref={recordBoardRef}>
                        <RecordTitle
                            title={title}
                            setTitle={setTitle}
                            goToMain={goToMain}
                        />
                    </Stack>
                    <form onSubmit={handleSubmit}>
                        <Stack>
                            <RecordBoard
                                handleSetImage={handleSetImage}
                                answer={answer}
                                inputCount={inputCount}
                                handleTextChange={handleAnswerChange}
                                showplaceholder='   .'
                            />
                        </Stack>
                        <RecordSaveButton recordsavebtnText=' ' />
                    </form>
                </Stack>
            </Stack>
            <AlertModal
                open={openModal}
                onClose={handleCloseModal}
                buttonText=' '
                onButtonClick={handleAcceptModal}
            >
                <Stack>
                    <Typography sx={modaltextstyle1}>
                          .
                    </Typography>
                    <Typography sx={modaltextstyle2}>
                         ?
                    </Typography>
                </Stack>
            </AlertModal>
        </Layout>
    );
};

export default RecordForm;

// Design
const contentstyle = {
    display: 'flex',
    alignItems: 'center',
    height: '100%',
    width: '100%',
    maxWidth: '600px',
    margin: '0 auto',
    padding: '1.5rem',
    boxSizing: 'border-box',
    minHeight: '100vh',
};

const modaltextstyle1 = {
    fontFamily: 'Noto Sans',
    fontSize: '0.92rem',
    fontWeight: '700',
    textAlign: 'center',
    color: '#7F5539',
};

const modaltextstyle2 = {
    fontFamily: 'Noto Sans',
    fontSize: '0.92rem',
    fontWeight: '700',
    textAlign: 'center',
    color: '#282828',
};
