import React, { useState, useRef, useEffect } from 'react';
import { Stack, Typography } from '@mui/material';
import RecordBoard from './components/RecordBoard';
import RecordSaveButton from './components/RecordSaveButton';
import Writer from './components/Writer';
import RecordUpper from './components/RecordUpper';
import AlertModal from '@/components/AlertModal';
import SelectSnowballObject from '@/components/SelectSnowballObject';
import useAxiosWithAuth from '@/utils/useAxiosWithAuth';
import { useParams, useNavigate } from 'react-router-dom';
import { useNicknameStore } from 'stores/useNicknameStore';
import Layout from '@/layouts/Layout';
import { Helmet } from 'react-helmet-async';
import { useSnackbarStore } from '@/stores/useSnackbarStore';
import { useUserStore } from '@/stores/useUserStore';

const GuestForm = () => {
    // useState  
    const [answer, setAnswer] = useState('');
    const [inputCount, setInputCount] = useState(0);
    const [writer, setWriter] = useState('');
    const [uploadedImage, setUploadedImage] = useState(null);
    const [object_name, setObjectName] = useState('');
    const [openModal, setOpenModal] = useState(false);
    // RecordBoard  ()
    const recordBoardRef = useRef(null); // RecordBoard 
    const writerRef = useRef(null); //Writer 
    const selectObjectRef = useRef(null);

    const { setSnackbarOpen } = useSnackbarStore();

    const params = useParams();
    const navigate = useNavigate();

    const { nickname } = useNicknameStore(); // {nickname, setNickname}
    const { userId } = useUserStore();

    useEffect(() => {
        if (params.userId === userId) {
            navigate(`/record/${userId}`);
        }
    }, []);

    //   
    const handleSetImage = (uploadedImage) => {
        setUploadedImage(uploadedImage);
    };

    //       
    const handleAnswerChange = (e) => {
        setAnswer(e.target.value.slice(0, 199));
        setInputCount(e.target.value.length);
    };

    //    &
    const axiosInstance = useAxiosWithAuth();
    const handleAcceptModal = async () => {
        // FormData        
        const formData = new FormData();
        if (uploadedImage) formData.append('image', uploadedImage);

        console.log('answer:', answer);
        console.log('image:', uploadedImage);
        console.log('writer:', writer);
        console.log('object_name:', object_name);

        await axiosInstance
            .post(`/api/share_memory/${params.userId}/write`, formData, {
                headers: {
                    'Content-Type': 'multipart/form-data',
                },
                params: {
                    title: writer,
                    answer: answer,
                    object_name: object_name,
                    writer: writer,
                    user_id: params.userId,
                },
            })
            .then(() => {
                navigate(`/complete/${params.userId}`);
            })
            .catch((error) => {
                console.log(error);
                setSnackbarOpen({
                    severity: 'error',
                    text: '  .  .',
                });
            });
    };

    //    
    const handleCloseModal = () => {
        setOpenModal(false);
    };

    //    
    const handleSubmit = (e) => {
        e.preventDefault();

        //   
        if (!object_name) {
            setSnackbarOpen({
                severity: 'warning',
                text: '  .',
            });
            selectObjectRef.current.scrollIntoView({ behavior: 'smooth' });
            return;
        } else if (!answer) {
            setSnackbarOpen({
                severity: 'warning',
                text: '  .',
            });
            recordBoardRef.current.scrollIntoView({ behavior: 'smooth' });
            return;
        }
        //  
        else if (!writer) {
            setSnackbarOpen({
                severity: 'warning',
                text: ' .',
            });
            writerRef.current.scrollIntoView({ behavior: 'smooth' });
            return;
        }

        //answer    
        setOpenModal(true);
    };

    return (
        <Layout
            snow
            overlay
            sx={{
                py: 3,
            }}
        >
            <Helmet>
                <title> -  </title>
                <meta name='description' content=' .' />
                <meta property='og:title' content=' -  ' />
                <meta property='og:description' content=' ' />
                <meta property='og:type' content='website' />
            </Helmet>
            <Stack sx={contentstyle}>
                <Stack>
                    <Stack ref={selectObjectRef}>
                        <RecordUpper sx={{ textAlign: 'left' }}></RecordUpper>
                    </Stack>
                    <Stack>
                        <SelectSnowballObject
                            snowballObject={object_name}
                            setSnowballObject={setObjectName}
                            mine={false}
                        />
                    </Stack>
                    <Stack>
                        <Typography sx={titlestyle}>
                            TO.&nbsp;{nickname}
                        </Typography>
                    </Stack>
                    <form onSubmit={handleSubmit}>
                        <Stack ref={recordBoardRef}>
                            <RecordBoard
                                handleSetImage={handleSetImage}
                                answer={answer}
                                inputCount={inputCount}
                                handleTextChange={handleAnswerChange}
                                showplaceholder='   .'
                            />
                        </Stack>
                        <Stack ref={writerRef}>
                            <Writer
                                fwriter={writer}
                                setfwriter={setWriter}
                            ></Writer>
                        </Stack>
                        <RecordSaveButton recordsavebtnText=' ' />
                    </form>
                </Stack>
            </Stack>
            <AlertModal
                open={openModal}
                onClose={handleCloseModal}
                buttonText=' '
                onButtonClick={handleAcceptModal}
            >
                <Stack>
                    <Typography sx={modaltextstyle1}>
                          .
                    </Typography>
                    <Typography sx={modaltextstyle2}>
                         ?
                    </Typography>
                </Stack>
            </AlertModal>
        </Layout>
    );
};

export default GuestForm;

//Design
const contentstyle = {
    display: 'flex',
    alignItems: 'center',
    height: '100%',
    width: '100%',
    maxWidth: '600px',
    margin: '0 auto',
    padding: '1.5rem',
    boxSizing: 'border-box',
};

const titlestyle = {
    color: 'custom.white',
    float: 'left',
    margin: '2.25rem 1rem 1rem',
};

const modaltextstyle1 = {
    fontFamily: 'Noto Sans',
    fontSize: '0.92rem',
    fontWeight: '700',
    textAlign: 'center',
    color: '#7F5539',
};
const modaltextstyle2 = {
    fontFamily: 'Noto Sans',
    fontSize: '0.92rem',
    fontWeight: '700',
    textAlign: 'center',
    color: '#282828',
};
