import AlertModal from '@/components/AlertModal';
import { CheckIcon, EditIcon } from '@/components/icons';
import useModal from '@/hooks/useModal';
import { Box, IconButton, Stack, styled, Typography } from '@mui/material';
import React, { useState, useEffect, useRef } from 'react';

export const StyledTypography = styled(Typography)(({ theme }) => ({
    color: theme.palette.custom.white,
    wordBreak: 'keep-all',
    whiteSpace: 'pre',
    padding: 0,
}));

const StyledIconButton = styled(IconButton)(() => ({
    width: '2rem',
    height: '2rem',
    boxSizing: 'border-box',
}));

const Input = styled('input')(({ theme }) => ({
    color: theme.palette.custom.main2,
    display: 'inline-block',
    outline: 'none',
    border: 'none',
    backgroundColor: 'transparent',
    padding: '0',
    '&:empty:before': {
        content: 'attr(placeholder)',
        color: theme.palette.custom.grey,
    },
    ...theme.typography.Heading1,
}));

const MainTitle = ({ snowball, setSnowballName, onError }) => {
    const [isEditing, setIsEditing] = useState(false);
    const [inputWidth, setInputWidth] = useState(0);
    const inputRef = useRef(null);
    const [currSnowball, setCurrSnowball] = useState(snowball);
    const [isSaving, setIsSaving] = useState(false);

    useEffect(() => {
        setCurrSnowball(snowball);
    }, [snowball]);

    const { isOpen, openModal, closeModal } = useModal();

    const cancelEdit = () => {
        setCurrSnowball(snowball);
        setIsEditing(false);
        closeModal();
    };

    const handleEdit = () => {
        setIsEditing(true);
    };

    const handleBlur = (event) => {
        if (isSaving) return;
        openModal();
    };

    const handleSnowballChange = (event) => {
        setCurrSnowball(event.target.value.slice(0, 10)); // 10   
    };

    const calculateInputWidth = () => {
        const canvas = document.createElement('canvas');
        const context = canvas.getContext('2d');
        const font = window.getComputedStyle(inputRef.current).font;
        context.font = font;
        const textWidth = context.measureText(
            currSnowball.length ? currSnowball : ''
        ).width;
        setInputWidth(textWidth);
    };

    useEffect(() => {
        if (isEditing) {
            calculateInputWidth();
        }
    }, [currSnowball, isEditing]);

    const onConfirmClick = (event) => {
        event?.preventDefault();
        setIsSaving(true);
        if (!currSnowball.length) return;
        setSnowballName(currSnowball)
            .then(() => setIsEditing(false))
            .catch((e) => {
                onError(e);
            });
        setIsSaving(false);
    };

    const handleKeyDown = (event) => {
        if (event.key === 'Enter') {
            console.log('enter');
            onConfirmClick();
        } else if (event.key === 'Escape') {
            openModal();
        }
    };

    return (
        <>
            <Stack
                direction={currSnowball.length > 5 ? 'column' : 'row'}
                sx={{
                    minHeight: '4.25rem',
                }}
            >
                {/* {currSnowball.length > 5 ? <br /> : ' '} */}

                <StyledTypography variant='Heading1'>
                    {isEditing ? (
                        <Input
                            ref={inputRef}
                            type='text'
                            value={currSnowball}
                            onChange={handleSnowballChange}
                            onBlur={handleBlur}
                            spellCheck='false'
                            onKeyDown={handleKeyDown}
                            style={{
                                width: `${inputWidth}px`,
                                transition: 'width 0.2s',
                            }}
                            placeholder=''
                            autoFocus
                        />
                    ) : (
                        <Box component={'span'} sx={{ color: 'custom.main2' }}>
                            {snowball}
                        </Box>
                    )}
                    {' '}
                </StyledTypography>
                <StyledTypography variant='Heading1'>
                    {''}
                    {isEditing ? (
                        <StyledIconButton
                            onMouseDown={onConfirmClick}
                            disabled={!currSnowball.length}
                        >
                            <CheckIcon
                                sx={{
                                    color: currSnowball.length
                                        ? 'custom.main1'
                                        : 'custom.grey',
                                }}
                            />
                        </StyledIconButton>
                    ) : (
                        <StyledIconButton onClick={handleEdit}>
                            <EditIcon sx={{ color: 'custom.white' }} />
                        </StyledIconButton>
                    )}
                </StyledTypography>
                <AlertModal
                    open={isOpen}
                    onClose={closeModal}
                    buttonText={''}
                    onButtonClick={cancelEdit}
                    keepMounted
                >
                    <Typography
                        variant='subtitle1'
                        align='center'
                        sx={{
                            color: 'custom.font',
                        }}
                    >
                        {'  ?'}
                    </Typography>
                </AlertModal>
            </Stack>
        </>
    );
};

export default MainTitle;
