import { Stack, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';
import DDayTitle from '@/pages/main/DDayTitle';
import Snowball from '@/pages/main/Snowball/Snowball';
import Layout from '@/layouts/Layout';
import useSWR from 'swr';
import Loading from '@/components/Loading';
import { StyledButton, MainContainer } from '@/pages/main/Main';
import Title from './Title';
import { useParams, useNavigate } from 'react-router-dom';
import { getDaysBeforeOpen } from '@/utils/getDaysBeforeOpen';
import axios from 'axios';
import { useNicknameStore } from 'stores/useNicknameStore';
import { Helmet } from 'react-helmet-async';
import { useUserStore } from '@/stores/useUserStore';

const Guest = () => {
    const [serverTime, setServerTime] = useState('');
    const navigate = useNavigate();
    const param = useParams();

    //   
    const { setNickname } = useNicknameStore();
    const { userId } = useUserStore();

    const snowballFetcher = (url) =>
        axios.get(url).then((res) => res.data.result.paginationData);

    const infoFetcher = (url) =>
        axios.get(url).then((res) => {
            setNickname(res.data.result.snowballName);
            return res.data.result;
        });

    const { data, isLoading, error } = useSWR(
        `${import.meta.env.VITE_API_URL}/api/capsule/${param.userId}/info`,
        infoFetcher,
        {
            onError: (error) => {
                console.error(error);
            },
        }
    );

    const onRecordClick = () => {
        navigate(`/guestrecord/${param.userId}`);
    };

    useEffect(() => {
        if (param.userId === userId) {
            navigate(`/main/${userId}?page=1`);
        }
    }, []);

    if (isLoading) return <Loading snow snowflake />;
    if (error) return <div>failed to load</div>;

    const daysLeft = getDaysBeforeOpen(serverTime);

    return (
        <Layout sx={{ overflow: 'hidden' }} snow snowflake>
            <Helmet>
                <title> - 2024   </title>
                <meta
                    name='description'
                    content='  2024  .'
                />
                <meta
                    property='og:title'
                    content=' - 2024   '
                />
                <meta
                    property='og:description'
                    content='  2024  .'
                />
                <meta property='og:type' content='website' />
            </Helmet>
            <MainContainer
                direction={'column'}
                justifyContent={'space-between'}
                alignContent={'center'}
                spacing={1}
            >
                <Stack direction={'column'} spacing={1} sx={{ flexGrow: 2 }}>
                    <Stack
                        direction={'row'}
                        justifyContent={'space-between'}
                        alignItems={'center'}
                    >
                        <DDayTitle />
                    </Stack>
                    <Title nickname={data?.snowballName ?? ''} />
                </Stack>

                <Snowball
                    isLoading={isLoading}
                    received={data?.receivedCount}
                    self={data?.selfCount}
                    fetcher={snowballFetcher}
                    setServerTime={setServerTime}
                />
                {daysLeft ? (
                    <StyledButton
                        variant={'contained'}
                        sx={{
                            flexGrow: 0,
                        }}
                    >
                        <Typography variant='title2'> </Typography>
                    </StyledButton>
                ) : (
                    <Stack
                        direction={'row'}
                        justifyContent={'space-between'}
                        spacing={'1rem'}
                        sx={{
                            flexGrow: 0,
                        }}
                    >
                        <StyledButton
                            variant={'contained'}
                            sx={{ flexGrow: 1, width: 'fit-content' }}
                        >
                            <Typography variant='title2'> </Typography>
                        </StyledButton>
                        <StyledButton
                            onClick={onRecordClick}
                            variant={'contained'}
                            sx={{ flexGrow: 2, width: 'fit-content' }}
                        >
                            <Typography variant='title2'>
                                 
                            </Typography>
                        </StyledButton>
                    </Stack>
                )}
            </MainContainer>
        </Layout>
    );
};

export default Guest;
