import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import PopupButton from './PopupButton';
import { useParams, useNavigate } from 'react-router-dom';
import { Portal } from '@mui/material';
import useAxiosWithAuth from '@/utils/useAxiosWithAuth';
import { useUserStore } from '@/stores/useUserStore';

const PopupWrapper = styled.div`
    display: ${(props) => (props.isOpen ? 'flex' : 'none')};
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 1000;
`;

const PopupContent = styled.div`
    position: relative;
    width: 80%;
    max-width: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
`;

const SvgWrapper = styled.div`
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
`;

const CloseButton = styled.span`
    position: absolute;
    top: 28px;
    right: 38px;
    cursor: pointer;
    font-size: 21px;
    font-weight: bold;
    z-index: 1;
`;

const TextWrapper = styled.div`
    position: absolute;
    top: 60px;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    z-index: 1;
`;

const StyledTitle = styled.div`
    color: #7f5539;
    text-align: center;
    -webkit-text-stroke-width: 0.7px;
    -webkit-text-stroke-color: var(--button1, #7f5539);
    font-size: 23px;
    font-style: normal;
    margin-top: 13px;
    line-height: 28px;
    font-family: 'Griun NltoTAENGGU', sans-serif;
`;

const StyledBodyText = styled.div`
    color: #282828;
    text-align: center;
    font-family: 'Noto Sans', sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    margin-top: 20px;
    line-height: 1.4;
    width: 220px;
    white-space: normal;
    word-break: break-word;

    span {
        font-family: 'Bigshot One', cursive;
    }
`;

const CheckboxWrapper = styled.div`
    display: flex;
    align-items: center;
    margin-top: 10px;
`;

const StyledCheckbox = styled.div`
    width: 20px;
    height: 20px;
    cursor: pointer;

    svg {
        display: block;
    }
`;

const CheckboxLabel = styled.label`
    font-size: 16px;
    color: white;
    margin-left: 8px;
    cursor: pointer;
`;

const ButtonWrapper = styled.div`
    margin-top: -45px;
    position: relative;
`;

const PopupPage = ({ isOpen, onClose, question, date }) => {
    const [isChecked, setIsChecked] = useState(false);
    const { userId } = useParams();
    const navigate = useNavigate();

    useEffect(() => {
        const lastPopupCheckedDate = localStorage.getItem('popupCheckedDate');
        const today = new Date().toLocaleDateString('ko-KR');

        if (lastPopupCheckedDate === today) {
            onClose();
        } else {
            setIsChecked(false); //        
        }
    }, []);

    const handleCheckboxChange = () => {
        const newCheckedStatus = !isChecked;
        setIsChecked(newCheckedStatus);

        const today = new Date().toLocaleDateString('ko-KR'); //      
        if (newCheckedStatus) {
            localStorage.setItem('popupCheckedDate', today);
            localStorage.setItem('popupCheckboxStatus', 'true');
        } else {
            localStorage.removeItem('popupCheckedDate'); //     
            localStorage.setItem('popupCheckboxStatus', 'false');
        }
    };

    const handleButtonClick = () => {
        onClose();
        navigate(`/record/${userId}`);
    };

    return (
        <Portal container={document.getElementById('capture-container')}>
            <PopupWrapper isOpen={isOpen}>
                <PopupContent>
                    <SvgWrapper>
                        <img src='/assets/Popup.svg' alt='popup' />
                        <CloseButton onClick={onClose}></CloseButton>
                        <TextWrapper>
                            <StyledTitle>
                                {question || '  ...'}
                            </StyledTitle>
                            <StyledBodyText>
                                <span>{date.split('')[0] || '01'}</span>{' '}
                                <span>
                                    {date.split(' ')[1]?.split('')[0] ||
                                        '01'}
                                </span>
                                  
                                <br />
                                  ?
                            </StyledBodyText>
                        </TextWrapper>
                    </SvgWrapper>
                    <CheckboxWrapper onClick={handleCheckboxChange}>
                        <StyledCheckbox>
                            {isChecked ? (
                                <svg
                                    xmlns='http://www.w3.org/2000/svg'
                                    width='20'
                                    height='20'
                                    viewBox='0 0 20 20'
                                    fill='none'
                                >
                                    <rect
                                        width='20'
                                        height='20'
                                        rx='4'
                                        fill='#7F5539'
                                    />
                                    <path
                                        d='M5 9L9 14L15.5 6'
                                        stroke='#FFFCFA'
                                        strokeWidth='3'
                                        strokeLinecap='round'
                                        strokeLinejoin='round'
                                    />
                                </svg>
                            ) : (
                                <svg
                                    xmlns='http://www.w3.org/2000/svg'
                                    width='20'
                                    height='20'
                                    viewBox='0 0 20 20'
                                    fill='none'
                                >
                                    <rect
                                        width='20'
                                        height='20'
                                        rx='4'
                                        fill='#FFFCFA'
                                    />
                                    <path
                                        d='M5 9L9 14L15.5 6'
                                        stroke='#D5D1CD'
                                        strokeWidth='3'
                                        strokeLinecap='round'
                                        strokeLinejoin='round'
                                    />
                                </svg>
                            )}
                        </StyledCheckbox>
                        <CheckboxLabel>    </CheckboxLabel>
                    </CheckboxWrapper>
                    <ButtonWrapper>
                        <PopupButton
                            text=' '
                            onClick={handleButtonClick}
                        />
                    </ButtonWrapper>
                </PopupContent>
            </PopupWrapper>
        </Portal>
    );
};

export default PopupPage;