import React from 'react';
import { useNavigate } from 'react-router-dom';
import Calendar from './Calendar/Calendar';
import Layout from '@/layouts/Layout';
import { Button, IconButton, Stack, Typography } from '@mui/material';
import useSWR from 'swr';
import useAxiosWithAuth from '@/utils/useAxiosWithAuth';
import Loading from '@/components/Loading';
import { CloseIcon } from '@/components/icons';
import { isRecordable } from '@/utils/isRecordable';
import { Helmet } from 'react-helmet-async';

const CalendarPage = () => {
    // 2025     year  Calendar->  day   .
    //   day  
    const year = 2024;

    const navigate = useNavigate();

    const axiosWithAuth = useAxiosWithAuth();

    const fetcher = (url) =>
        axiosWithAuth.get(url).then((res) => res.data.result);

    const { data, isLoading } = useSWR(`/calendar/data`, fetcher, {
        onError: (error) => {
            console.error(error);
        },
    });

    const handleClose = () => {
        navigate(-1);
    };

    const handleSave = () => {
        const imgUrl = `/assets/calendar/image.png`;

        const a = document.createElement('a');
        a.href = imgUrl;
        a.download = '-.png';
        a.click();
    };

    if (isLoading) return <Loading />;

    const lastDayWritten = data.written_array[31];

    return (
        <Layout
            snow
            overlay
            sx={{
                py: 3,
            }}
        >
            <Helmet>
                <title> - 2024   </title>
                <meta
                    name='description'
                    content='     .'
                />
                <meta
                    property='og:title'
                    content=' - 2024   '
                />
                <meta
                    property='og:description'
                    content='     .'
                />
                <meta property='og:type' content='website' />
            </Helmet>
            <IconButton
                sx={{
                    color: 'custom.grey',
                    width: '1.5rem',
                    height: '1.5rem',
                }}
                onClick={handleClose}
            >
                <CloseIcon />
            </IconButton>
            <Stack
                sx={{
                    mt: 4,
                    mb: 3,
                    width: '100%',
                }}
                direction={'column'}
                spacing={3}
                alignItems={'center'}
            >
                <Stack
                    sx={{
                        paddingX: ['0.1rem', '2.4rem'],
                        width: '100%',
                        boxSizing: 'border-box',
                    }}
                    spacing={0.75}
                >
                    <Typography variant='title3' sx={{ color: 'custom.grey' }}>
                        {!isRecordable(data.server_time) || lastDayWritten
                            ? ' !'
                            : '    !'}
                    </Typography>
                    <Typography
                        variant='Heading2'
                        sx={{ color: 'custom.white' }}
                    >
                        {'    '}
                        <Typography
                            variant={'Heading2'}
                            component={'span'}
                            sx={{ color: 'custom.main1' }}
                        >
                            {`${data.my_memory_count}`}
                        </Typography>
                    </Typography>
                </Stack>
                <Calendar
                    serverTime={data.server_time}
                    hasWritten={data.written_array}
                    year={year}
                />
                {(!isRecordable(data.server_time) || lastDayWritten) && (
                    <Button
                        variant='contained'
                        sx={{
                            backgroundColor: 'custom.button1',
                            width: ['100%', '87.5%'],
                            height: '4rem',
                            borderRadius: '1.25rem',
                        }}
                        onClick={handleSave}
                    >
                        <Typography
                            variant='title2'
                            sx={{
                                color: 'custom.white',
                            }}
                        >
                            {' '}
                        </Typography>
                    </Button>
                )}
            </Stack>
        </Layout>
    );
};

export default CalendarPage;
