import { useNavigate } from 'react-router-dom';
import { useEffect } from 'react';
import styled from 'styled-components';
import Snowfall from 'react-snowfall';
import axios from 'axios';
import '@dotlottie/player-component';
import useAuthStore from 'stores/useAuthStore';
import { saveTokenFromURL } from '@/utils/saveTokenFromURL';

const FullScreenSnowfall = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    pointer-events: none;
    z-index: 0;
`;

const Container = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100vw;
    max-width: 600px;
    background: linear-gradient(180deg, #0b0a1b 0%, #27405e 100%);
    position: relative;
    overflow: hidden;
    margin: 0 auto;
    background-color: white;
    z-index: 1;
`;

const SubTitle = styled.p`
    font-size: 19px;
    color: #fff;
    position: absolute;
    bottom: 300px;
    transform: translateX(-50%);
    left: 50%;
    white-space: nowrap;
    line-height: 1.5;
`;

const Button = styled.button`
    display: inline-flex;
    height: 62px;
    padding: 20px 109px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    flex-shrink: 0;
    border: none;
    border-radius: 20px;
    background: var(--main2, #ddb892);
    box-shadow: 0px 0px 4px 0px rgba(40, 40, 40, 0.2);
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    position: absolute;
    bottom: 40px;
    z-index: 10;
`;

const BottomImage = styled.img`
    position: absolute;
    bottom: 0;
    height: 100vh;
    bottom: -335px;
    width: 100vw;
    max-width: 600px;
    object-fit: contain;
`;

const SnowballPage = () => {
    const navigate = useNavigate();
    const { login } = useAuthStore();

    useEffect(() => {
        saveTokenFromURL(login);
    }, [login]);

    //      API 
    const handleCreateSnowball = () => {
        const token = localStorage.getItem('token');
        const snowballAPI = `${import.meta.env.VITE_API_URL}/api/capsule`;
        if (token) {
            axios
                .post(
                    snowballAPI,
                    {},
                    {
                        headers: {
                            Authorization: `Bearer ${localStorage.getItem('token')}`, // Authorization   
                        },
                    }
                )
                .then((response) => {
                    const snowballData = response.data.result;

                    //     
                    console.log(' :', snowballData);

                    //        
                    if (
                        snowballData &&
                        snowballData.id &&
                        snowballData.snowball_name &&
                        snowballData.shared_link
                    ) {
                        localStorage.setItem('snowball_id', snowballData.id);
                        localStorage.setItem(
                            'snowball_name',
                            snowballData.snowball_name
                        );
                        localStorage.setItem(
                            'snowball_link',
                            snowballData.shared_link
                        );
                        console.log('   ');
                    } else {
                        console.error(
                            '   :',
                            snowballData
                        );
                    }

                    //   
                    navigate(`/main/${snowballData.id}?page=1`);
                })
                .catch((error) => {
                    console.error('  :', error);
                });
        } else {
            console.error(' URL ');
        }
    };

    return (
        <>
            <FullScreenSnowfall>
                <Snowfall
                    color='#ffffffaa'
                    snowflakeCount={70}
                    speed={[0, 0.5]}
                    wind={[0, 0.5]}
                    radius={[0.5, 3]}
                />
            </FullScreenSnowfall>
            <Container>
                <Snowfall
                    color='#ffffffaa'
                    snowflakeCount={70}
                    speed={[0, 0.5]}
                    wind={[0, 0.5]}
                    radius={[0.5, 3]}
                />

                {/* Lottie   */}
                <dotlottie-player
                    src='https://lottie.host/699c976a-3836-4dd2-9858-edaa2d16b866/KDNntsFKi1.json'
                    background='transparent'
                    speed='1'
                    style={{
                        width: '300px',
                        height: '300px',
                        position: 'absolute',
                        bottom: '410px',
                        left: '50%',
                        transform: 'translateX(-50%)',
                    }}
                    loop
                    autoplay
                ></dotlottie-player>

                <SubTitle>
                        
                    <br />
                       
                </SubTitle>

                <Button onClick={handleCreateSnowball}> </Button>

                <BottomImage
                    src={'/assets/snowball_bottom.svg'}
                    alt='Snow background'
                />
            </Container>
        </>
    );
};

export default SnowballPage;
