import React from 'react';
import html2canvas from 'html2canvas';
import { IconButton } from '@mui/material';
import ShareIcon from '@mui/icons-material/Share';
import { useSnackbarStore } from '@/stores/useSnackbarStore';

const ImgShareButton = ({ title, url }) => {
    const { setSnackbarOpen } = useSnackbarStore();

    const handleImageShare = async () => {
        try {
            const captureImg = document.getElementById('capture-container');
            const canvas = await html2canvas(captureImg, {
                backgroundColor: null,
            });
            const dataUrl = canvas.toDataURL('image/png');
            const blob = await (await fetch(dataUrl)).blob();
            const file = new File([blob], 'layout-image.png', {
                type: 'image/png',
            });

            if (navigator.share) {
                await navigator.share({
                    title: title,
                    text: '   ',
                    files: [file],
                    url: url,
                });
                setSnackbarOpen({
                    text: ' .',
                    severity: 'success',
                });
            } else {
                const link = document.createElement('a');
                link.href = dataUrl;
                link.download = 'layout-image.png';
                link.click();

                await navigator.clipboard.writeText(url);
                setSnackbarOpen({
                    text: '  : ' + url,
                    severity: 'success',
                });
            }
        } catch (error) {
            console.error(' :', error);
            setSnackbarOpen({
                text: ' .   .',
                severity: 'error',
            });
        }
    };

    return (
        <IconButton
            aria-label=' '
            sx={{ width: '1.5rem', height: '1.5rem' }}
            onClick={handleImageShare}
        >
            <ShareIcon sx={{ color: 'custom.grey' }} />
        </IconButton>
    );
};

export default ImgShareButton;
