import React from 'react';
import {
    Box,
    Button,
    IconButton,
    Modal,
    Stack,
    styled,
    Typography,
} from '@mui/material';
import { CloseIcon } from './icons';

/**
 * AlertModal  modal  ,   .
 *
 *
 * @param {boolean} open - modal open 
 * @param {function} onClose - modal  
 * @param {string} buttonText -  
 * @param {function} onButtonClick -     
 * @param {any} children - modal 
 * @param  {...any} props -  props, Modal  
 * @returns
 */

// mui     
const modalContainerStyle = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    boxShadow: 24,
    borderRadius: '1.25rem',
    height: 'fit-content',
    width: ['80vw', 'fit-content'],
    maxWidth: '20rem',
    overflow: 'hidden',
};

const modalDescriptionStyle = {
    px: [2, 6],
    pb: 3,
    wordBreak: 'keep-all',
};

const ModalButton = styled(Button)(({ theme }) => ({
    padding: '1.25rem 0',
    borderRadius: 0,
    backgroundColor: theme.palette.custom.button1,
}));

const AlertModal = ({
    open,
    onClose,
    buttonText,
    onButtonClick,
    children,
    ...props
}) => {
    return (
        <Modal
            open={open}
            onClose={onClose}
            aria-describedby='alert-modal-description'
            {...props}
        >
            <Stack
                direction={'column'}
                justifyContent={'center'}
                sx={modalContainerStyle}
            >
                <Stack
                    direction={'column'}
                    p={2}
                    spacing={3}
                    sx={{
                        bgcolor: 'background.paper',
                    }}
                >
                    <Stack direction={'row'} justifyContent={'flex-end'}>
                        <IconButton
                            onClick={onClose}
                            sx={{
                                w: '1.5rem',
                                h: '1.5rem',
                            }}
                        >
                            <CloseIcon />
                        </IconButton>
                    </Stack>
                    <Box
                        sx={modalDescriptionStyle}
                        id='alert-modal-description'
                    >
                        {children}
                    </Box>
                </Stack>
                <ModalButton variant='contained' onClick={onButtonClick}>
                    <Typography variant='title2'>{buttonText}</Typography>
                </ModalButton>
            </Stack>
        </Modal>
    );
};

export default AlertModal;
